
import { defineComponent, computed, ref, watch } from 'vue'
import { useStore } from '@/store'
import { usePermission } from '@/hooks/usePermission'
import { ElMessage, ElMessageBox } from 'element-plus'

import TlTable from '@/base-ui/table'

export default defineComponent({
  props: {
    contentTableConfig: {
      type: Object,
      required: true
    },
    pageName: {
      type: String,
      required: true
    }
  },
  components: {
    TlTable
  },
  emits: ['newBtnClick', 'EditBtnClick'],
  setup(props, { emit }) {
    const store = useStore()

    const isCreate = usePermission(props.pageName, 'create')
    const isUpdate = usePermission(props.pageName, 'update')
    const isDelete = usePermission(props.pageName, 'delete')
    const isQuery = usePermission(props.pageName, 'query')

    // console.log(props.contentTableConfig.title)

    // 双向绑定pageInfo
    const pageInfo = ref({ currentPage: 1, pageSize: 10 })
    watch(pageInfo, () => getPageData())

    // 发送网络请求  注意：因为接口定义页数是从0开始的，而table分页只能从第一页开始，所以offset的当前页要-1来发送请求
    const getPageData = (queryInfo: any = {}) => {
      if (!isQuery) return // 如果没有查询权限，直接返回

      // 派发保存当前查询条件的信息
      store.dispatch('system/saveQueryInfoAction', {
        pageInfo: pageInfo.value,
        queryInfo: queryInfo
      })

      store.dispatch('system/getPageListAction', {
        pageName: props.pageName,
        queryInfo: {
          offset: (pageInfo.value.currentPage - 1) * pageInfo.value.pageSize,
          size: pageInfo.value.pageSize,
          ...queryInfo
        }
      })
    }
    getPageData()

    // 从vuex中获取数据
    const datalist = computed(() =>
      store.getters[`system/getListData`](props.pageName)
    )
    const dataCount = computed(() =>
      store.getters[`system/getListCount`](props.pageName)
    )

    // 获取其他的动态插槽名称
    const otherPropSlots = props.contentTableConfig?.propsList.filter(
      (item: any) => {
        if (item.slotName === 'createAt') return false
        if (item.slotName === 'updateAt') return false
        if (item.slotName === 'handler') return false
        return true
      }
    )

    // 这里可以进行选中行的数据操作
    const handleSelectionChange = (e: any) => {
      console.log(e)
    }

    // 删除/编辑/新建操作
    const handleDeleteClick = (item: any) => {
      ElMessageBox.confirm('确实删除吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          if (props.pageName === 'goods') {
            await store.dispatch('system/deletePageDataAction', {
              pageName: props.pageName,
              id: item.id,
              imageId: item.imageId
            })
          } else {
            await store.dispatch('system/deletePageDataAction', {
              pageName: props.pageName,
              id: item.id
            })
          }
          ElMessage({
            type: 'success',
            message: '删除成功'
          })
        })
        .catch(() => {
          ElMessage({
            type: 'info',
            message: '取消删除'
          })
        })
    }

    const handleNewClick = () => {
      emit('newBtnClick')
    }

    const handleEditClick = (item: any) => {
      emit('EditBtnClick', item)
    }

    return {
      props,
      datalist,
      getPageData,
      dataCount,
      pageInfo,
      otherPropSlots,
      isCreate,
      isUpdate,
      isDelete,
      handleSelectionChange,
      handleDeleteClick,
      handleNewClick,
      handleEditClick
    }
  }
})
